$color1: hsla(84%, 51%, 47%, 1);
$color2: hsla(0%, 0%, 9%, 1);
$color3: hsla(180%, 2%, 45%, 1);
$color4: hsla(0%, 0%, 33%, 1);
$color5: hsla(90%, 23%, 17%, 1);

/* RGB */
$color1: rgba(133, 183, 59, 1);
$color2: rgba(22, 22, 22, 1);
$color3: #E9E9E9;
$color4: rgba(83, 83, 83, 1);
$color5: rgba(43, 53, 33, 1);


* {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif; }


.background {
    background-color: $color1;
    width: 100%;
    height: 700px;
    overflow: hidden;
    .pulsating-circle {
        position: absolute;
        left: 22%;
        top: 130px;
        transform: translateX(-50%) translateY(-50%);
        width: 170px;
        height: 170px;
        z-index: 1;
        &:before {
            content: '';
            z-index: 2;
            position: absolute;
            display: block;
            width: 300%;
            height: 300%;
            box-sizing: border-box;
            margin-left: -100%;
            margin-top: -100%;
            border-radius: 50%;
            background-color: #9dc463;
            animation: pulse-ring 1.1s cubic-bezier(0.215, 0.61, 0.355, 1) infinite; }
        &:after {
            content: '';
            z-index: 3;
            position: absolute;
            left: 0;
            top: 0;
            display: block;
            width: 100%;
            height: 100%;
            background-color: $color3;
            border-radius: 50%;
            box-shadow: 0 0 10px rgba(0,0,0,.3);
            animation: pulse-dot 1.1s cubic-bezier(0.455, 0.03, 0.515, 0.955) -.4s infinite; } }
    h1 {
        position: absolute;
        left: 178px;
        top: -120px;
        letter-spacing: 10px;
        color: $color5;
        font-size: 80px;
        margin-top: 200px;
        z-index: 99; }
    img {
        position: absolute;
        left: 600px;
        top: 240px;
        width: 600px; } }








// ---------KEYFRAMNES-------------



@keyframes pulse-ring {
  0% {
    transform: scale(.33); }

  70%, 100% {
    opacity: 0; } }



@keyframes pulse-dot {
  0% {
    transform: scale(.8); }

  50% {
    transform: scale(1); }

  100% {
    transform: scale(.8); } }



